'use client';
import { useEffect, useState } from 'react';
import { DataService } from '@/utils/api';
// We are NOT importing the Chart or ImageUpload yet to see if they are the problem

export default function Home() {
    const [pestData, setPestData] = useState<any[]>([]);

    useEffect(() => {
        DataService.getPestDetections().then(data => setPestData(data)).catch(err => console.log(err));
    }, []);

    return (
        <div style={{ backgroundColor: '#020617', color: 'white', minHeight: '100vh', padding: '50px' }}>
            <h1 style={{ color: '#10b981' }}>EXPEL AI - EMERGENCY TEST MODE</h1>
            <p>If you can see this, your main page is working!</p>

            <div style={{ marginTop: '30px' }}>
                <h2>Recent Pests:</h2>
                {pestData.length > 0 ? (
                    pestData.map((p, i) => (
                        <div key={i} style={{ borderBottom: '1px solid #334155', padding: '10px 0' }}>
                            {p.pestType} - {p.count} found
                        </div>
                    ))
                ) : (
                    <p>No data in database yet.</p>
                )}
            </div>
        </div>
    );
}